
li[data-v-9] {
  list-style-type: none;
  border: 1px solid #e1e8ed;
}
.card[data-v-9]:hover {
  background-color: #f5f8fa;
}
.content[data-v-9] {
  padding: 9px 12px;
}

/* avatat */
img.header__avatar[data-v-9] {
  float:left;
  width: 48px;
  height: 48px;

  border-radius: 5px;
  margin-right: 10px;
}

/* screanname */
.header__screanname[data-v-9], .header__date[data-v-9] {
  font-size: 13px;
  color: #8899a6;
}
.header__screanname > s[data-v-9]{
  text-decoration: none;
}
.header__date[data-v-9]::before {
  content: "\00b7";
}
.blog-card[data-v-9] {
  margin-left: 58px;
  margin-top: 20px;
}

/* text */
.text[data-v-9] {
  margin-left: 58px;
}
.text__content[data-v-9] {
  margin: 0;
  white-space: pre-line;
}

/* footer */
.footer[data-v-9] {
  margin-top: 10px;
  margin-left: 58px;
}
.footer > div[data-v-9] {
  display: inline-block;
  position: relative;
  width: 80px;
}
.footer button[data-v-9] {
  padding: 3px 5px;
  border: 0;
  background-color: transparent;
  color: #aab8c2;
  font-size: 16px;
}
.footer__reply:hover i[data-v-9] {
  color: #7708a3;
}
.footer__retweet:hover i[data-v-9] {
  color: #19cf86;
}
.footer__heart:hover i[data-v-9] {
  color: #e81c4f;
}



