
.header__back[data-v-6] {
  background-color: #323232;
  height: 400px;
}
.header__container[data-v-6] {
  height: 400px;
  width: 900px;
  margin: 0 auto;

  position: relative;

  background-image: url("../assets/header.png");
  background-position: right bottom;
  background-repeat: no-repeat;
  background-size: 900px 300px;
}
.header__avatar[data-v-6] {
  height: 200px;
  width: 200px;

  position: absolute;
  left: 10px;
  bottom: -87px;

  background-color: #fff;
  border: 5px solid #fff;
  border-radius: 12px;
  box-shadow: 0 1px 1px rgba(136, 153, 166, 0.15);
}
.avatar__icon[data-v-6] {
  height: 200px;
  width: 200px;

  border-radius: 8px;
}



