
.topbar[data-v-5] {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;

  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  z-index: 1000;
}
.global-navbar[data-v-5] {
  position: relative;
  width: 100%;
  height: 46px;

  background-color: #fff;
}
h1[data-v-5] {
  margin: 0;
}
.title[data-v-5] {
  position: absolute;
  left: 50%;
  margin-left: -14px;

  line-height: 46px;
  color: #333333;
}
ul[data-v-5] {
  margin: 0;
  padding: 0;
  list-style: none;
}
ul > li[data-v-5] {
  display: inline-block;
}
ul > li a[data-v-5] {
  display: block;
  height: 46px;

  padding: 0 14px 0 4px;
  text-decoration: none;
  color: #66757f;

  border-bottom: 5px solid transparent;
  transition: all .15s ease-in-out;
}
ul > li a[data-v-5]:hover {
  height: 42px;
  color: #7708a3;
  border-bottom: 5px solid #7708a3;
}
ul > li a > i[data-v-5] {
  margin: 11px 0 0 10px;
  width: 23px;
  height: 23px;
  font-size: 23px;
}
ul > li a > span[data-v-5] {
  margin-left: 5px;
  color: inherit;
}



