
.time-line[data-v-8] {
  display: inline-block;
  box-sizing: border-box;
  border-radius: 4px;

  vertical-align: top;

  width: 70%;
  min-width: 500px;

  background-color: #fff;

  margin-bottom: 50px;
}
ol[data-v-8] {
  padding: 0;
  margin: 0;
}
.time-line__end[data-v-8] {
  height: 60px;
  text-align: center;
  line-height: 60px;

  border: 1px solid #e1e8ed;
}
.time-line__end > i[data-v-8] {
  font-size: 24px;
  color: #66757f;
}



