
.navbar[data-v-7] {
  height: 50px;
  background-color: #fff;
}
.mini-profile[data-v-7] {
  display: inline-block;
  width: 30%;
  min-width: 200px;
  box-sizing: border-box;
}
.profile-navbar[data-v-7] {
  display: inline-block;
  width: 70%;
  min-width: 500px;
  box-sizing: border-box;
}
ul[data-v-7] {
  list-style: none;
  margin: 0;
  padding: 0;
}
ul > li[data-v-7] {
  display: inline-block;
  width: 85px;
  padding: 0 15px;

  border-bottom: 5px solid transparent;
  transition: all .15s ease-in-out;
}
li > a[data-v-7] {
  display: block;
  text-decoration: none;
  height: 50px;
  text-align: center;
  line-height: 40px;
  color: #66757f;
  font-size: 1.2em;
}
li[data-v-7]:hover,
li > a[data-v-7]:hover,
.router-link-active[data-v-7] {
  height: 46px;
  color: #7708a3;
  border-bottom: 5px solid #7708a3;
}



