
.profile[data-v-4] {
  display: inline-block;
  box-sizing: border-box;
  vertical-align: top;

  margin-top: 37px;
  margin-bottom: 25px;

  padding: 0 15px;

  width: 30%;
  min-width: 200px;
}
h1[data-v-4], h2[data-v-4] {
  margin: 0 0 5px 0;
}
i[data-v-4] {
  color: #8899a6;
  width: 18px;
  height: 18px;
  text-align: center;

  font-size: 18px;
}
a[data-v-4] {
  text-decoration: none;
}
a[data-v-4]:hover {
  text-decoration: underline;
}
ul[data-v-4] {
  margin-top: 5px;
  padding: 0;
  list-style-type: none;
}
li[data-v-4] {
  margin-bottom: 2px;
}
.profile__name[data-v-4] {
  font-size: 24px;
}
.profile__screanname[data-v-4] {
  font-size: 14px;
}
.profile__screanname > a[data-v-4] {
  color: #8899a6;
}
.profile__bio[data-v-4] {
  /*word-break: break-all;*/
}
.profile__url[data-v-4], .profile__certificates[data-v-4] {
  margin-top: 5px;
  border-top: 1px solid #e1e8ed;
}



